<template>
  <popup
    v-model="show"
    :close-on-click-overlay="false"
    class="alert">
    <div class="panda" v-if="showPanda">
      <img src="/static/images/panda.png">
    </div>
    <h1 class="confirm-header">{{ title }}</h1>
    <h2 class="text">{{contentText}}</h2>
    <button class="btn" @click="clickCancel">{{ cancelText }}</button>
    <button class="btn" @click="clickConfirm">{{ confirmText }}</button>
  </popup>
</template>

<script>
import { Popup } from 'vant'

export default {
  data() {
    return {
      show: false,
    }
  },
  props: {
    title: {
      type: String,
      require: false,
      default: '',
    },
    contentText: {
      type: String,
      require: false,
      default: '',
    },
    text: {
      type: String,
      require: false,
      default: '',
    },
    confirmText: {
      type: String,
      require: false,
      default: '确认',
    },
    cancelText: {
      type: String,
      require: false,
      default: '取消',
    },
    showPanda: {
      type: Boolean,
      require: false,
      default: false
    }
  },
  methods: {
    clickCancel() {
      this.show = false
      this.cancel()
    },
    clickConfirm() {
      this.show = false
      this.confirm()
    },
    cancel() {},
    confirm() {}
  },
  components: {
    Popup
  }
}
</script>
