// 设置全局的vant组件
import Vue from 'vue'
import {
  Toast,
  Lazyload
} from 'vant'
import {
  formatDate
} from '@/assets/libs/utils'

const lazyloadOptions = {
  loading: '/assets/images/avatar_error.jpg',
  error: '/assets/images/avatar_error.jpg',
  preload: 1.3,
  attempt: 1
}

Vue.use(Lazyload, lazyloadOptions)

const errorAlert = (data) => {
  let time = formatDate().dateString
  let cookie = document.cookie
  let ua = navigator.userAgent

  let errorLog = `TIME: [${time}], COOKIE: [${cookie}], USERAGENT: [${ua}]`
  if (data && data.errors) {
    let errKey = Object.keys(data.errors)[0],
      errMsg = data.errors[errKey][0].err_msg,
      token = window.sessionStorage.getItem('token')
    Vue.prototype.$whiteToast({
      text: errMsg,
      delay: 2500
    })
    console.error(`${errorLog}, ERRKEY: ${errKey}, ERRMSG: ${errMsg}, TOKEN: ${token}`)
  }
}

Object.defineProperty(Vue.prototype, '$error', {
  value: errorAlert
})

Vue.prototype.$loading = ({
  duration = 0,
  forbidClick = true
} = {}) => {
  return Toast.loading({
    duration: duration,
    forbidClick: forbidClick
  })
}

Vue.prototype.$toast = ({
  text,
  cb
}) => {
  Toast({
    duration: 2500,
    forbidClick: true,
    message: text
  })
  if (cb) {
    setTimeout(cb, 2501);
  }
}
