import Vue from 'vue';
import Router from 'vue-router';

import auth from './modules/auth';

Vue.use(Router);

const router = new Router({
  mode: 'hash',
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      redirect: '/auth/signin'
    }
  ].concat(auth)
});

export default router;
