/* eslint-disable */
import Vue from 'vue'
import AlertComponent from './Alert.vue'

export default {
  install(Vue) {
    const Alert = Vue.extend(AlertComponent)
    let cache = ''

    function fn(options = {}) {

      let vm = cache || (cache = new Alert)
      if (!vm.$el) {
        // 异步挂载
        let $vm = vm.$mount()
        document.body.appendChild($vm.$el)
      }
      Object.assign(vm, options);
      Vue.prototype.$popupInstance = vm
      vm.show = true
    }
    Vue.prototype.$alert = fn
  }
}
