import Vue from 'vue';
import router from '~/router';
import { AccountService } from '~/service/api';
import { stat } from 'fs';

const state = {
  user: {},
  checkCode: ''
};
const actions = {
  // 普通游客登录
  async NORMAL_LOGIN(ctx, { mobile, password }) {
    const { state, msg } = await AccountService.normalLogin({
      mobile,
      password
    });
    if (state === '101') {
      Vue.prototype.$toast({ text: msg });
    } else {
      Vue.prototype.$toast({ text: msg });
    }
  },
  // 注册
  async REGISTER(ctx, { mobile, password }) {
    Vue.prototype.$loading()
    const { state, msg } = await AccountService.register({
      mobile,
      password
    });
    Vue.prototype.$loading({
      duration: 1
    })
    if (state === '101') {
      Vue.prototype.$toast({ text: msg });
      router.push({
        name: 'signin'
      })
    } else {
      Vue.prototype.$toast({ text: msg });
    }
  },
  // 游客账号登录
  async LOGIN({ commit }, { mobile, password }) {
    Vue.prototype.$loading()
    const { state, msg, data } = await AccountService.login({
      mobile,
      password
    });
    Vue.prototype.$loading({
      duration: 1
    })
    if (state === '101') {
      commit('LOGIN', data)
      Vue.prototype.$toast({ text: msg });
    } else {
      Vue.prototype.$toast({ text: msg });
    }
  },
  // 找回密码
  async FIND_PASSWORD({ commit }, { mobile, password }) {
    Vue.prototype.$loading()
    const { state, msg } = await AccountService.findPassword({
      mobile,
      password
    });
    Vue.prototype.$loading({
      duration: 1
    })
    if (state === '101') {
      Vue.prototype.$toast({ text: msg });
    } else {
      Vue.prototype.$toast({ text: msg });
    }
  },
  // 获取验证码
  async SEND_CODE({ commit }, { mobile }) {
    Vue.prototype.$loading()
    const { state, msg } = await AccountService.sendCode({
      mobile,
    });
    Vue.prototype.$loading({
      duration: 1
    })
    if (state === '101') {
      Vue.prototype.$toast({ text: msg });
    } else {
      Vue.prototype.$toast({ text: msg });
    }
  },
  // 注册前验证验证码
  async CHECK_CODE({ commit }, { mobile, code }) {
    Vue.prototype.$loading()
    const { state, msg } = await AccountService.checkCode({
      mobile,
      code
    });
    Vue.prototype.$loading({
      duration: 1
    })
    if (state === '101') {
      commit('CHECK_CODE', true)
      Vue.prototype.$toast({ text: msg });
    } else {
      commit('CHECK_CODE', false)
      Vue.prototype.$toast({ text: msg });
    }
  },
};
const mutations = {
  LOGIN(state, params) {
    window.localStorage.setItem('user', JSON.stringify(params))
    state = Object.assign(state.user, params)
  },
  CHECK_CODE(state, params) {
    state.checkCode = params
  }
};
const getters = {};

export default {
  state,
  actions,
  mutations,
  getters
};
