import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store';
import '@/assets/libs/vant';
import Fastclick from 'fastclick';
import { sync } from 'vuex-router-sync-previous';

import Toast from '@/assets/plugins/toast';
import Alert from '@/assets/plugins/alert';
import Confirm from '@/assets/plugins/confirm';
import { Notify, CellGroup, Field, Button } from 'vant';
import '@/assets/css/reset.less';
import '~/assets/css/common.less';

Vue.config.productionTip = false;

// init vuex-router-sync
sync(store, router);

// use plugins
Vue.use(Toast);
Vue.use(Alert);
Vue.use(Confirm);
Vue.use(Notify);
Vue.use(Field);
Vue.use(Button).use(CellGroup);
/* eslint-disable no-new */
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
Fastclick.attach(document.body);
