/* eslint-disable */
import Vue from 'vue'
import ToastComponent from './Toast.vue'

export default {
  install(Vue) {
    const Toast = Vue.extend(ToastComponent)
    let cache = ''

    function fn(options = {}) {

      let vm = cache || (cache = new Toast)
      if (!vm.$el) {
        // 异步挂载
        let $vm = vm.$mount()
        document.body.appendChild($vm.$el)
      }
      Object.assign(vm, options);
      vm.show = true
      return new Promise((resolve, reject) => {
        setTimeout(resolve, vm.delay)
      })
    }
    Vue.prototype.$whiteToast = fn
  }
}
