import { api } from '@/api/axios';
import qs from 'qs';

 const API_URL = 'http://52game666.cn';
//const API_URL = '';

export const ApiService = {
  post(url, params) {
    return api.post(`${API_URL}/${url}`, params).catch(error => {
      console.log(error);
    });
  }
};

export const AccountService = {
  // 普通游客登录
  normalLogin({
    mobile,
    password,
    appkey
  } = {}) {
    return ApiService.post('newgame/index.php/Mobile/Indexer/loginer', qs.stringify({
      mobile,
      password,
      appkey
    }));
  },
  // 游客账号登录
  login({
    mobile,
    password,
    appkey
  } = {}) {
    return ApiService.post('newgame/index.php/Mobile/Indexer/yklogin', qs.stringify({
      mobile: 'yk' + mobile,
      password,
      appkey
    }));
  },
  // 获取验证码
  sendCode({
    mobile,
  } = {}) {
    return ApiService.post('newgame/index.php/Mobile/Index/sendcode', qs.stringify({
      mobile,
    }));
  },
  // 注册前验证验证码
  checkCode({
    mobile,
    code,
    appkey
  } = {}) {
    return ApiService.post('newgame/index.php/Mobile/Index/CheckCode', qs.stringify({
      mobile,
      code,
      appkey
    }));
  },
  // 注册
  register({
    mobile,
    num = 1,
    password,
    appkey
  } = {}) {
    return ApiService.post('newgame/index.php/Mobile/Indexer/registereder', qs.stringify({
      mobile,
      num,
      password,
      appkey
    }));
  },
  // 找回密码
  findPassword({
    mobile,
    num = 1,
    password,
    appkey
  } = {}) {
    return ApiService.post('newgame/index.php/Mobile/Index/findpw', qs.stringify({
      mobile,
      num,
      password,
      appkey
    }));
  },
};
