<template>
  <popup
    v-model="show"
    :close-on-click-overlay="false"
    class="toast">
    <div class="panda" v-if="showPanda">
      <img src="/static/images/panda.png">
    </div>
    <span v-html="text"></span>
  </popup>
</template>

<script>
import { Popup } from 'vant'

export default {
  data() {
    return {
      show: false,
    }
  },
  props: {
    text: {
      type: String,
      require: true,
      default: '',
    },
    delay: {
      type: Number,
      require: false,
      default: 2500,
    },
    showPanda: {
      type: Boolean,
      require: false,
      default: false
    }
  },
  methods: {
    callback() {}
  },
  watch: {
    show() {
      if (this.show) {
        setTimeout(() => {
          this.show = false;
          this.callback()
        }, this.delay);
      }
    }
  },
  components: {
    Popup
  }
}
</script>
<style lang="less" scoped>
  .toast{
    .panda{
      position: absolute;
      width: 120px;
      left: 50%;
      margin-left: -60px;
      top: -70px;
    }
    width: 280px;
    padding: 22px;
    font-size: 16px;
    border-radius: 10px;
    text-align: center;
  }
</style>

