<template>
  <div id="app"><router-view v-if="isRouterAlive"></router-view></div>
</template>
<script>
export default {
  name: 'App',
  provide() {
    return {
      reload: this.reload
    };
  },
  data() {
    return {
      isRouterAlive: true
    };
  },
  methods: {
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(() => {
        this.isRouterAlive = true;
      });
    }
  }
};
</script>
<style lang="less">
#app {
  height: 100%;
}
.btn-disabled {
  color: #fff !important;
  background-color: #989896 !important;
}
</style>
