/*
 * 添加拦截器并返回axios实例
 */
import Vue from 'vue';
import axios from 'axios';
import { formatDate } from '@/assets/libs/utils';

function setInterceptors(axiosInstance) {
  // request interceptor
  /* eslint-disable */ //Disable for process
  axiosInstance.interceptors.request.use(
    req => {
      return req;
    },
    error => {
      console.error(error);
      return Promise.reject(error);
    }
  );

  // response interceptor
  axiosInstance.interceptors.response.use(
    res => {
      return res.data;
    },
    error => {
      logError(error);
      if (error.code === 'ECONNABORTED') {
        Vue.prototype.$whiteToast({
          text: '网络错误，请检查网络连接刷新后重试',
          delay: 2000
        });
      }
      return Promise.reject(error);
    }
  );
}

// create axios instance
function getAxios(settings, timeout = 10000) {
  const axiosInstance = axios.create(settings);
  axios.defaults.timeout = timeout;
  setInterceptors(axiosInstance);
  return axiosInstance;
}

function logError(error) {
  let url = error.config.url;
  let headers = JSON.stringify(error.config.headers);
  let time = formatDate().dateString;
  let cookie = document.cookie;
  let ua = navigator.userAgent;

  let errorMsg = `MESSAGE: [${error.message}], STATUS: [${
    error.request.status
  }], TIME: [${time}], COOKIE: [${cookie}], URL: [${url}], HEADERS: [${headers}], USERAGENT: [${ua}]`;
  console.error(errorMsg);
  Vue.prototype.$errorMsg = errorMsg;
}

export const api = getAxios({
  baseURL: '/api/',
  withCredentials: true,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded'
  }
});
