/* eslint-disable */
import Vue from 'vue'
import moment from 'moment'
// import wx from 'wx'

const week = ['周日', '周一', '周二', '周三', '周四', '周五', '周六']

export const isWeiXin = () => {
  let ua = window.navigator.userAgent.toLowerCase()
  return /microMessenger/ig.test(ua)
}

export const isQQ = () => {
  let ua = window.navigator.userAgent.toLowerCase()
  /* eslint eqeqeq: off */
  let isqq = ua.match(/ qq/i) == ' qq'
  /* eslint eqeqeq: off */
  if (ua.match(/mqqbrowser/i) == 'mqqbrowser') {
    isqq = false
  }
  return isqq
}

export const userAgent = () => {
  var u = navigator.userAgent
  return {
    trident: u.indexOf('Trident') > -1,
    presto: u.indexOf('Presto') > -1,
    webKit: u.indexOf('AppleWebKit') > -1,
    gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1,
    mobile: !!u.match(/AppleWebKit.*Mobile.*/),
    ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/),
    android: u.indexOf('Android') > -1 || u.indexOf('Adr') > -1,
    iPhone: u.indexOf('iPhone') > -1,
    iPad: u.indexOf('iPad') > -1,
    webApp: u.indexOf('Safari') == -1,
    weixin: u.indexOf('MicroMessenger') > -1,
    qq: u.match(/\sQQ/i) == 'qq'
  };
}

export const isUrl = (str = '') => {
  return /^((ht|f)tps?):\/\/[\w\-]+(\.[\w\-]+)+([\w\-\.,@?^=%&:\/~\+#]*[\w\-\@?^=%&\/~\+#])?$/.test(str)
}

export const formatDate = (date = moment(), format = 'YYYY-MM-DD HH:mm:ss') => {
  let today = moment()
  if (!date) date = moment()
  if (!(typeof date === 'object')) {
    let d
    try {
      d = moment(date)
    } catch (e) {
      console.error(e)
    }
    if (d.toString() === 'Invalid Date') {
      return console.error('日期格式不正确，请输入日期字符串、时间戳(毫秒)或Date对象\n', `Error Date Input: ${date}`)
    } else {
      date = d
    }
  } else {
    date = moment(date)
  }

  let map = {
    Y: date.format('YYYY'),
    M: date.format('MM'),
    D: date.format('DD'),
    h: date.format('HH'),
    m: date.format('mm'),
    s: date.format('ss'),
    w: date.format('d')
  }

  return {
    date: date._d,
    dateString: date.format(format),
    standardDayString: date.format('YYYY-MM-DD'),
    standardString: date.format('YYYY-MM-DD HH:mm:ss'),
    momentDate: date,
    dateMap: map,
    week: today.format('DDD') === date.format('DDD') ? '今天' : week[date.day()]
  }
}

const throttle = (delay, action, ss) => {
  var last = 0
  return function () {
    var curr = +new Date()
    if (curr - last > delay) {
      action.apply(this, arguments)
      last = curr
    }
  }
}
Vue.prototype.$throttle = throttle
