/* eslint-disable */
import Vue from 'vue'
import ConfirmComponent from './Confirm.vue'

export default {
  install(Vue) {
    const Confirm = Vue.extend(ConfirmComponent)
    let cache = ''

    function fn(options = {}) {
      let vm = cache || (cache = new Confirm)
      if (!vm.$el) {
        // 异步挂载
        let $vm = vm.$mount()
        document.body.appendChild($vm.$el)
      }
      Object.assign(vm, options);
      Vue.prototype.$popupInstance = vm
      vm.show = true
    }
    Vue.prototype.$confirm = fn
  }
}
