<template>
  <popup
    v-model="show"
    :close-on-click-overlay="false"
    class="alert">
    <div class="panda" v-if="showPanda">
      <img src="/static/images/panda.png">
    </div>
    <h1 class="alert-header">{{title}}</h1>
    <div class="alert-main" v-html="text"></div>
    <div class="alert-main" v-html="text2"></div>
    <button class="btn" @click="buttonClick">{{buttonText}}</button>
  </popup>
</template>

<script>
import { Popup } from 'vant'

export default {
  data() {
    return {
      show: false,
    }
  },
  props: {
    title: {
      type: String,
      require: false,
      default: '',
    },
    text: {
      type: String,
      require: true,
      default: '',
    },
    text2: {
      type: String,
      require: true,
      default: '',
    },
    buttonText: {
      type: String,
      require: false,
      default: '确认',
    },
    showPanda: {
      type: Boolean,
      require: false,
      default: false
    }
  },
  methods: {
    buttonClick() {
      this.show = false
      this.callback()
    },
    callback() {}
  },
  components: {
    Popup
  }
}
</script>
